
.marker-cluster-pie g.arc {
  fill-opacity: 0.5;
}
.marker-cluster-pie-label {
  font-size: 14px;
  font-weight: bold;
  font-family: sans-serif;
}
.marker-cluster {
  border-radius: 35px !important;
  background-color: #fff;
}
.marker-cluster-call-pie-label {
  font-size: 14px;
  font-weight: bold;
  font-family: serif;
}
.marker-cluster-call {
  border-radius: 35px !important;
  background-color: #efefef;
}



